@import url(//db.onlinewebfonts.com/c/a4e40941cd04772dbe49aa5790ad5905?family=OPTICalculator);

$padWidth: 100px;
$padHeight: 80px;
$volumeIconSize: 16px;

body {
  user-select: none;
}

#root {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

#logo-header {
  margin: 40px;
  width: 100%;
}

.inner-container {
  position: relative;
  width: 660px;
  text-align: center;
  background-color: lighten(grey, 20%);

  #pad-container {
    display: inline-block;
    width: $padWidth * 3 + 32;
    height: $padHeight * 3 + 32;
    margin: 20px;
    
    .drum-pad {
      position: relative;
      float: left;
      width: $padWidth;
      height: $padHeight;
      margin-right: 10px;
      margin-bottom: 10px;
      padding-top: 35px;
      border-radius: 5px;
      border-style: solid;
      box-sizing: border-box;
      cursor: pointer;
      box-shadow: 0 6px #999;
    }

    .drum-pad:active {
      box-shadow: 0 3px #666;
      transform: translateY(4px);
    }
  }
  #config-container {
    display: inline-block;
    width: 240px;
    height: $padHeight * 3 + 32;
    vertical-align: top;

    #power-button {
      width: 100px;
      margin: auto;

      .fa-power-off {
        font-size: 24px;
      }
    }

    #display {
      width: 200px;
      background-color: gray;
      margin: 30px auto 15px auto;
      padding: 15px;
      box-sizing: border-box;
      font-family: OPTICalculator;
    }

    #sound-selector {
      margin: 40px 0;
      width: 100%;

      #sound-slider {
        width: 75%;
        height: 15px;
        border-radius: 5px;
        margin: auto 10px;
      }

      .fa-volume-off {
        font-size: $volumeIconSize;
        vertical-align: top;
      }

      .fa-volume-up {
        font-size: $volumeIconSize;
        vertical-align: top;
      }
    }

    #bank-selector {
      position: relative;
      display: inline-block;
      width: 60px;
      height: 34px;
    
      input {
        opacity: 0;
        width: 0;
        height: 0;
      }

      .bank-slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: #ccc;
        -webkit-transition: .4s;
        transition: .4s;
      }

      .bank-slider::before {
        position: absolute;
        content: "";
        height: 26px;
        width: 26px;
        left: 4px;
        bottom: 4px;
        background-color: white;
        -webkit-transition: .4s;
        transition: .4s;
      }

      input:checked + .bank-slider {
        background-color: gray;
      }

      input:focus + .bank-slider {
        box-shadow: 0 0 1px gray;
      }

      input:checked + .bank-slider:before {
        -webkit-transform: translateX(26px);
        -ms-transform: translateX(26px);
        transform: translateX(26px);
      }

      .bank-slider.round {
        border-radius: 34px;
      }

      .bank-slider.round::before {
        border-radius: 50%;
      }

      #bank-title {
        margin-top: 25px;
      }
    }
  }
}
