@import url(//db.onlinewebfonts.com/c/a4e40941cd04772dbe49aa5790ad5905?family=OPTICalculator);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

#root {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; }

#logo-header {
  margin: 40px;
  width: 100%; }

.inner-container {
  position: relative;
  width: 660px;
  text-align: center;
  background-color: #b3b3b3; }
  .inner-container #pad-container {
    display: inline-block;
    width: 332px;
    height: 272px;
    margin: 20px; }
    .inner-container #pad-container .drum-pad {
      position: relative;
      float: left;
      width: 100px;
      height: 80px;
      margin-right: 10px;
      margin-bottom: 10px;
      padding-top: 35px;
      border-radius: 5px;
      border-style: solid;
      box-sizing: border-box;
      cursor: pointer;
      box-shadow: 0 6px #999; }
    .inner-container #pad-container .drum-pad:active {
      box-shadow: 0 3px #666;
      -webkit-transform: translateY(4px);
              transform: translateY(4px); }
  .inner-container #config-container {
    display: inline-block;
    width: 240px;
    height: 272px;
    vertical-align: top; }
    .inner-container #config-container #power-button {
      width: 100px;
      margin: auto; }
      .inner-container #config-container #power-button .fa-power-off {
        font-size: 24px; }
    .inner-container #config-container #display {
      width: 200px;
      background-color: gray;
      margin: 30px auto 15px auto;
      padding: 15px;
      box-sizing: border-box;
      font-family: OPTICalculator; }
    .inner-container #config-container #sound-selector {
      margin: 40px 0;
      width: 100%; }
      .inner-container #config-container #sound-selector #sound-slider {
        width: 75%;
        height: 15px;
        border-radius: 5px;
        margin: auto 10px; }
      .inner-container #config-container #sound-selector .fa-volume-off {
        font-size: 16px;
        vertical-align: top; }
      .inner-container #config-container #sound-selector .fa-volume-up {
        font-size: 16px;
        vertical-align: top; }
    .inner-container #config-container #bank-selector {
      position: relative;
      display: inline-block;
      width: 60px;
      height: 34px; }
      .inner-container #config-container #bank-selector input {
        opacity: 0;
        width: 0;
        height: 0; }
      .inner-container #config-container #bank-selector .bank-slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: #ccc;
        -webkit-transition: .4s;
        transition: .4s; }
      .inner-container #config-container #bank-selector .bank-slider::before {
        position: absolute;
        content: "";
        height: 26px;
        width: 26px;
        left: 4px;
        bottom: 4px;
        background-color: white;
        -webkit-transition: .4s;
        transition: .4s; }
      .inner-container #config-container #bank-selector input:checked + .bank-slider {
        background-color: gray; }
      .inner-container #config-container #bank-selector input:focus + .bank-slider {
        box-shadow: 0 0 1px gray; }
      .inner-container #config-container #bank-selector input:checked + .bank-slider:before {
        -webkit-transform: translateX(26px);
        transform: translateX(26px); }
      .inner-container #config-container #bank-selector .bank-slider.round {
        border-radius: 34px; }
      .inner-container #config-container #bank-selector .bank-slider.round::before {
        border-radius: 50%; }
      .inner-container #config-container #bank-selector #bank-title {
        margin-top: 25px; }

